*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  position: relative;
  background: linear-gradient(to left top, #add2f8, #add2f8);
}

.check {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 25px;
  transform: translate(-45%, -80%);
  box-sizing: border-box;
  border-left: 4px solid white;
  border-bottom: 4px solid white;
  opacity: 0;
  transition: transform 0.6s, opacity 0.8s;
}

.check.show {
  transform: translate(-45%, -80%) rotate(-50deg);
  opacity: 1;
}

.circle {
  position: absolute;
  width: 250px;
  height: 250px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #5d91f3;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0px 0px 29px -6px black;
}

.circle.small {
  width: 100px;
  height: 100px;
}

.circle:hover {
  background-color: #5b98f8;
}

.circle:active {
  background-color: #5b9fff;
}

.mail {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 250%);
  transition: transform 0.5s;
}

.mail.show {
  transform: translate(-50%, -50%);
}

.mail.hide {
  transform: translate(120%, -50%);
}

.mail-back__paper {
  width: 110px;
  height: 80px;
  background: #e8e8e8;
}

.mail-back__paper:after {
  content: '';
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 100%;
  left: 0;
  border-top: 0px solid transparent;
  border-left: 55px solid transparent;
  border-right: 55px solid transparent;
  border-bottom: 40px solid white;
  transition: border-bottom 0.2s;
}

.mail-back__paper.animate:after {
  border-bottom: 0px solid white;
}

.mail-front__paper {
  width: 110px;
  height: 80px;
  border-top: 35px solid transparent;
  border-left: 55px solid #efefef;
  border-right: 55px solid #efefef;
  border-bottom: 45px solid white;
}

.mail-front__paper:after {
  position: absolute;
  content: '';
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
  border-top: 0px solid #e2e2e2;
  border-left: 55px solid transparent;
  border-right: 55px solid transparent;
  border-bottom: 35px solid transparent;
  transition: border-top 0.5s;
}

.mail-front__paper.animate:after {
  border-top: 45px solid #e2e2e2;
}

.paper {
  width: 100px;
  height: 100px;
  background-size: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: transform 0.5s;
}

.paper-1 {
  transform: translate(-80%, -20%);
}

.paper-2 {
  transform: translate(-50%, -50%);
}

.paper-3 {
  transform: translate(-20%, -80%);
}

.paper.folder {
  transform: translate(-50%, -50%);
}

.paper.hide {
  display: none;
}

.instruction {
  width: 100%;
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, 0);
  text-align: center;
  color: #fff;
  line-height: 2;
}

@media screen and (max-width: 400px) {
  .instruction {
    font-size: 22px;
    top: 5%;
  }
}

@media screen and (max-height: 700px) {
  .instruction {
    font-size: 22px;
    top: 5%;
  }
}

@media screen and (max-height: 500px) {
  .instruction {
    font-size: 16px;
  }
}

@media screen and (max-height: 500px) {
  .instruction {
    top: 0;
  }
  .instruction span {
    display: none;
  }
}
